html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  color: var(--color-text);
}

img {
  height: 100%;
  max-width: 100%;
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  font-size: initial;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.burger {
  @include burger;

  display: none;
  z-index: 100;

  @include tablet {
    display: block;
  }
}

.section-offset {
  padding-bottom: 80px;

  @include small-tablet {
    padding-bottom: 50px;
  }
  @include mobile {
    padding-bottom: 35px;
  }
}

.section-offset-big {
  padding-bottom: 128px;

  @include tablet {
    padding-bottom: 100px;
  }
  @include small-tablet {
    padding-bottom: 75px;
  }
  @include mobile {
    padding-bottom: 50px;
  }
}

.section-title {
  font-family: "Nunito", sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 50px;
  color: var(--color-cod);

  @include tablet {
    font-size: 30px;
    line-height: 40px;
  }
  @include mobile {
    font-size: 25px;
    line-height: 35px;
  }
}

.descr {
  font-size: 16px;
  line-height: 26px;
  color: var(--color-text);

  @include mobile {
    font-size: 14px;
    line-height: 18px;
  }
}

.btn {
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  color: var(--color-light);
  padding: 15px 31px;
  background-color: var(--color-accent);
  border-radius: 4px;
  border: 1px solid var(--color-accent);
  transition: color 0.3s, background-color 0.3s;
  &:hover {
    border: 1px solid var(--color-black);
    color: var(--color-black);
  }

  @include mobile {
    font-size: 14px;
    line-height: 20px;
  }
}

.logo {
  display: inline-block;
  &__img {
    vertical-align: middle;
  }
}
